import {
	postPic,
	appUpdInfo,
	getInfoByToken
} from '../../api/auth.js'
import {
	putUserInfo
} from '../../setting/putUserInfo.js'
export default {
	data() {
		return {
			userInfo: null,
			accountStatus: false,
			picStatus: false,
			files: [],
		}
	},
	onShow() {
		let userInfo = uni.getStorageSync('userInfo');
		this.userInfo = userInfo;
	},
	methods: {
		hideAccountModal() {
			this.accountStatus = false
		},
		showAccountModal() {
			this.accountStatus = true
		},
		submitAccount() {
			if (!this.userInfo.account) {
				uni.showToast({
					icon: 'none',
					title: '请先输入昵称'
				})
				return;
			}
			let info = {
				account: this.userInfo.account
			}
			appUpdInfo(info).then(data => {
				console.log(data);
				// 修改信息关键部分
				putUserInfo(data).then(data => {
					console.log(data)
				}).catch(err => {
					console.log(err)
				})
			}).catch(err => {
				console.log(err);
			})
		},
		hidePicModal() {
			this.picStatus = false
		},
		showPicModal() {
			this.picStatus = true
		},
		/* 上传图片 */
		uploadImg(file) {
			return new Promise((resolve, reject) => {
				uni.uploadFile({
					url: "https://www.sweetqianba.com/fastDfs/uploadFile",
					filePath: file,
					name: 'file',
					success(res) {
						console.log(res.data)
						this.userInfo.pic = 'https://' + res.data.URL;
					},
					fail(err) {
						reject(err)
					}
				})
			})
		},
		submitPic() {
			this.uploadImg(this.files[0]).then(data => {
				console.log(data)
				if (data.success === '上传成功') {
					uni.showToast({
						icon: 'none',
						title: '上传成功'
					})
					let info = {
						pic: 'https://' + data.URL
					}
					appUpdInfo(info).then(data => {
						console.log(data);
						// 修改信息关键部分
						putUserInfo(data).then(data => {
							this.accountStatus = false;
							this.picStatus = false;
						}).catch(err => {
							console.log(err)
						})
					}).catch(err => {
						console.log(err);
					})
				}
			}).catch(err => {
				console.log(err)
			})

		},
		/* 删除图片 */
		removeImg(index) {
			this.files.splice(index, 1)
		},
		/* 预览图片 */
		previewImage(e) {
			uni.previewImage({
				current: e.currentTarget.id,
				urls: this.files,
			})
		},
		/* 选择图片 */
		chooseImage(e) {
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'],
				success: (res) => {
					this.files = this.files.concat(res.tempFilePaths)
				}
			})
		},
	}
}
